import React, { useState, useEffect, useRef } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import {
  Zap, LayoutDashboard, ClipboardList, Calculator, Users, LogOut,
  Menu, X, User, BookOpen, FileText, DollarSign, Target, Megaphone,
  Settings, ChevronDown, Search, Bell, Check
} from 'lucide-react';

interface Notification {
  id: string;
  title: string;
  message: string;
  time: string;
  read: boolean;
  type: 'info' | 'success' | 'warning';
}

const Navigation: React.FC = () => {
  const { user, logout } = useAuth();
  const navigate = useNavigate();
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [showMoreMenu, setShowMoreMenu] = useState(false);
  const [showSearch, setShowSearch] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [showNotifications, setShowNotifications] = useState(false);
  const [notifications, setNotifications] = useState<Notification[]>([
    { id: '1', title: 'Welcome!', message: 'Start by exploring your dashboard', time: 'Just now', read: false, type: 'success' },
    { id: '2', title: 'Tip', message: 'Use Alt+D to quickly access Dashboard', time: '1 hour ago', read: false, type: 'info' },
    { id: '3', title: 'New Feature', message: 'Check out the Marketing tools!', time: '2 hours ago', read: true, type: 'info' },
  ]);
  const searchInputRef = useRef<HTMLInputElement>(null);

  const handleLogout = () => {
    logout();
    navigate('/');
  };

  const markAsRead = (id: string) => {
    setNotifications(prev => prev.map(n => n.id === id ? { ...n, read: true } : n));
  };

  const markAllAsRead = () => {
    setNotifications(prev => prev.map(n => ({ ...n, read: true })));
  };

  const unreadCount = notifications.filter(n => !n.read).length;

  const searchPages = [
    { path: '/dashboard', label: 'Dashboard', keywords: ['home', 'overview', 'metrics', 'stats'] },
    { path: '/business-plan', label: 'Business Plan', keywords: ['plan', 'model', 'strategy', 'mission', 'vision'] },
    { path: '/calculators', label: 'Calculators', keywords: ['calc', 'pricing', 'roi', 'profit', 'break-even', 'cash flow'] },
    { path: '/customers', label: 'Customers', keywords: ['crm', 'clients', 'contacts', 'leads'] },
    { path: '/finances', label: 'Finances', keywords: ['money', 'budget', 'transactions', 'expenses', 'income'] },
    { path: '/strategy', label: 'Strategy', keywords: ['swot', 'goals', 'competitors', 'okr', 'milestones'] },
    { path: '/marketing', label: 'Marketing', keywords: ['campaigns', 'social', 'email', 'calendar', 'content'] },
    { path: '/documents', label: 'Documents', keywords: ['invoice', 'proposal', 'contract', 'templates'] },
    { path: '/resources', label: 'Resources', keywords: ['guides', 'learning', 'help', 'tips'] },
    { path: '/settings', label: 'Settings', keywords: ['profile', 'preferences', 'dark mode', 'export'] },
  ];

  const filteredPages = searchQuery.trim()
    ? searchPages.filter(page =>
        page.label.toLowerCase().includes(searchQuery.toLowerCase()) ||
        page.keywords.some(k => k.includes(searchQuery.toLowerCase()))
      )
    : [];

  const handleSearchSelect = (path: string) => {
    navigate(path);
    setSearchQuery('');
    setShowSearch(false);
  };

  // Handle keyboard shortcuts
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      // Ctrl/Cmd + K for search
      if ((e.ctrlKey || e.metaKey) && e.key.toLowerCase() === 'k') {
        e.preventDefault();
        setShowSearch(true);
        setTimeout(() => searchInputRef.current?.focus(), 100);
      }
      // Escape to close search
      if (e.key === 'Escape') {
        setShowSearch(false);
        setShowNotifications(false);
      }
      if (e.altKey) {
        switch (e.key.toLowerCase()) {
          case 'd':
            e.preventDefault();
            navigate('/dashboard');
            break;
          case 'c':
            e.preventDefault();
            navigate('/calculators');
            break;
          case 'k':
            e.preventDefault();
            navigate('/customers');
            break;
        }
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [navigate]);

  const mainNavItems = [
    { path: '/dashboard', label: 'Dashboard', icon: LayoutDashboard },
    { path: '/business-plan', label: 'Business Plan', icon: ClipboardList },
    { path: '/calculators', label: 'Calculators', icon: Calculator },
    { path: '/customers', label: 'Customers', icon: Users },
  ];

  const moreNavItems = [
    { path: '/finances', label: 'Finances', icon: DollarSign },
    { path: '/strategy', label: 'Strategy', icon: Target },
    { path: '/marketing', label: 'Marketing', icon: Megaphone },
    { path: '/documents', label: 'Documents', icon: FileText },
    { path: '/resources', label: 'Resources', icon: BookOpen },
    { path: '/settings', label: 'Settings', icon: Settings },
  ];

  return (
    <nav className="main-nav">
      <div className="nav-container">
        <NavLink to="/dashboard" className="nav-brand">
          <Zap size={28} className="brand-icon" />
          <span className="brand-name">Expedium</span>
        </NavLink>

        <button
          className="mobile-menu-btn"
          onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
        >
          {mobileMenuOpen ? <X size={24} /> : <Menu size={24} />}
        </button>

        <div className={`nav-links ${mobileMenuOpen ? 'open' : ''}`}>
          {mainNavItems.map((item) => (
            <NavLink
              key={item.path}
              to={item.path}
              className={({ isActive }) => `nav-link ${isActive ? 'active' : ''}`}
              onClick={() => setMobileMenuOpen(false)}
            >
              <item.icon size={18} />
              <span>{item.label}</span>
            </NavLink>
          ))}

          {/* More dropdown */}
          <div className="nav-more">
            <button
              className={`nav-link more-btn ${showMoreMenu ? 'active' : ''}`}
              onClick={() => setShowMoreMenu(!showMoreMenu)}
            >
              <span>More</span>
              <ChevronDown size={16} className={showMoreMenu ? 'rotated' : ''} />
            </button>
            {showMoreMenu && (
              <div className="more-dropdown">
                {moreNavItems.map((item) => (
                  <NavLink
                    key={item.path}
                    to={item.path}
                    className={({ isActive }) => `dropdown-link ${isActive ? 'active' : ''}`}
                    onClick={() => { setShowMoreMenu(false); setMobileMenuOpen(false); }}
                  >
                    <item.icon size={16} />
                    <span>{item.label}</span>
                  </NavLink>
                ))}
              </div>
            )}
          </div>

          {/* Mobile-only links */}
          <div className="mobile-only-links">
            {moreNavItems.map((item) => (
              <NavLink
                key={item.path}
                to={item.path}
                className={({ isActive }) => `nav-link ${isActive ? 'active' : ''}`}
                onClick={() => setMobileMenuOpen(false)}
              >
                <item.icon size={18} />
                <span>{item.label}</span>
              </NavLink>
            ))}
          </div>
        </div>

        {/* Search and Notifications */}
        <div className="nav-actions">
          <button
            className="nav-action-btn search-btn"
            onClick={() => { setShowSearch(true); setTimeout(() => searchInputRef.current?.focus(), 100); }}
            title="Search (Ctrl+K)"
          >
            <Search size={20} />
          </button>

          <div className="notification-wrapper">
            <button
              className={`nav-action-btn notification-btn ${unreadCount > 0 ? 'has-unread' : ''}`}
              onClick={() => setShowNotifications(!showNotifications)}
            >
              <Bell size={20} />
              {unreadCount > 0 && <span className="notification-badge">{unreadCount}</span>}
            </button>

            {showNotifications && (
              <div className="notification-dropdown">
                <div className="notification-header">
                  <h3>Notifications</h3>
                  {unreadCount > 0 && (
                    <button className="mark-all-read" onClick={markAllAsRead}>
                      <Check size={14} />
                      Mark all read
                    </button>
                  )}
                </div>
                <div className="notification-list">
                  {notifications.length > 0 ? (
                    notifications.map(notification => (
                      <div
                        key={notification.id}
                        className={`notification-item ${!notification.read ? 'unread' : ''}`}
                        onClick={() => markAsRead(notification.id)}
                      >
                        <div className={`notification-dot ${notification.type}`} />
                        <div className="notification-content">
                          <strong>{notification.title}</strong>
                          <p>{notification.message}</p>
                          <span className="notification-time">{notification.time}</span>
                        </div>
                      </div>
                    ))
                  ) : (
                    <div className="notification-empty">No notifications</div>
                  )}
                </div>
              </div>
            )}
          </div>
        </div>

        <div className={`nav-user ${mobileMenuOpen ? 'open' : ''}`}>
          <div className="user-info">
            <User size={18} />
            <span>{user?.name}</span>
          </div>
          <button className="logout-btn" onClick={handleLogout}>
            <LogOut size={18} />
            <span>Logout</span>
          </button>
        </div>
      </div>

      {/* Click outside to close more menu */}
      {showMoreMenu && (
        <div className="dropdown-overlay" onClick={() => setShowMoreMenu(false)} />
      )}

      {/* Click outside to close notifications */}
      {showNotifications && (
        <div className="dropdown-overlay" onClick={() => setShowNotifications(false)} />
      )}

      {/* Global Search Modal */}
      {showSearch && (
        <div className="search-modal-overlay" onClick={() => setShowSearch(false)}>
          <div className="search-modal" onClick={e => e.stopPropagation()}>
            <div className="search-input-wrapper">
              <Search size={20} />
              <input
                ref={searchInputRef}
                type="text"
                placeholder="Search pages... (Esc to close)"
                value={searchQuery}
                onChange={e => setSearchQuery(e.target.value)}
                autoFocus
              />
              <kbd>Esc</kbd>
            </div>
            {filteredPages.length > 0 && (
              <div className="search-results">
                {filteredPages.map(page => (
                  <button
                    key={page.path}
                    className="search-result-item"
                    onClick={() => handleSearchSelect(page.path)}
                  >
                    <span>{page.label}</span>
                    <span className="search-keywords">{page.keywords.slice(0, 3).join(', ')}</span>
                  </button>
                ))}
              </div>
            )}
            {searchQuery && filteredPages.length === 0 && (
              <div className="search-no-results">
                No pages found for "{searchQuery}"
              </div>
            )}
          </div>
        </div>
      )}
    </nav>
  );
};

export default Navigation;
