import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import {
  LayoutDashboard, TrendingUp, Users, Target, Calendar, DollarSign,
  ArrowRight, CheckCircle, Clock, AlertCircle, Zap, BarChart3,
  FileText, Lightbulb, ArrowUpRight, ArrowDownRight
} from 'lucide-react';
import { Customer, MonthlyGoal } from '../types';

interface QuickStat {
  label: string;
  value: string | number;
  change?: number;
  icon: React.ElementType;
  color: string;
}

interface ActivityItem {
  id: string;
  type: 'customer' | 'goal' | 'task' | 'milestone';
  message: string;
  time: string;
}

const Dashboard: React.FC = () => {
  const { user } = useAuth();
  const [customers, setCustomers] = useState<Customer[]>([]);
  const [goals, setGoals] = useState<MonthlyGoal[]>([]);
  const [showOnboarding, setShowOnboarding] = useState(false);
  const [onboardingStep, setOnboardingStep] = useState(0);

  useEffect(() => {
    const savedCustomers = localStorage.getItem(`expedium_customers_${user?.id}`);
    const savedGoals = localStorage.getItem(`expedium_goals_${user?.id}`);
    const hasSeenOnboarding = localStorage.getItem(`expedium_onboarding_${user?.id}`);

    if (savedCustomers) setCustomers(JSON.parse(savedCustomers));
    if (savedGoals) setGoals(JSON.parse(savedGoals));
    if (!hasSeenOnboarding) setShowOnboarding(true);
  }, [user]);

  const completeOnboarding = () => {
    localStorage.setItem(`expedium_onboarding_${user?.id}`, 'true');
    setShowOnboarding(false);
  };

  const onboardingSteps = [
    {
      title: 'Welcome to Expedium!',
      description: 'Your all-in-one business management platform. Let\'s take a quick tour of the features available to you.',
      icon: Zap
    },
    {
      title: 'Business Plan Builder',
      description: 'Answer guided questions to create your business model. Our AI assistant provides personalized advice based on your answers.',
      icon: FileText
    },
    {
      title: 'Financial Calculators',
      description: 'Use 14+ calculators for pricing, ROI, break-even analysis, and more. Test different scenarios before making decisions.',
      icon: DollarSign
    },
    {
      title: 'Customer Management',
      description: 'Track customers, log interactions, set follow-up reminders, and monitor your outreach goals.',
      icon: Users
    },
    {
      title: 'Strategy & Marketing Tools',
      description: 'Build SWOT analyses, track competitors, plan marketing campaigns, and set business milestones.',
      icon: Target
    },
    {
      title: 'You\'re All Set!',
      description: 'Explore the dashboard to see your business overview. Click any card to dive deeper into that area.',
      icon: CheckCircle
    }
  ];

  // Calculate stats
  const activeCustomers = customers.filter(c => c.status === 'active').length;
  const leads = customers.filter(c => c.status === 'lead').length;
  const currentMonth = new Date().toISOString().slice(0, 7);
  const currentGoal = goals.find(g => g.month === currentMonth);
  const goalProgress = currentGoal ? Math.round((currentGoal.completed / currentGoal.targetOutreach) * 100) : 0;

  const upcomingFollowUps = customers
    .filter(c => c.nextFollowUp && new Date(c.nextFollowUp) >= new Date())
    .sort((a, b) => new Date(a.nextFollowUp).getTime() - new Date(b.nextFollowUp).getTime())
    .slice(0, 5);

  const overdueFollowUps = customers
    .filter(c => c.nextFollowUp && new Date(c.nextFollowUp) < new Date())
    .length;

  const quickStats: QuickStat[] = [
    { label: 'Active Customers', value: activeCustomers, change: 12, icon: Users, color: '#10b981' },
    { label: 'Leads', value: leads, change: 5, icon: Target, color: '#6366f1' },
    { label: 'Monthly Goal', value: `${goalProgress}%`, icon: TrendingUp, color: '#f59e0b' },
    { label: 'Follow-ups Due', value: overdueFollowUps, icon: Clock, color: overdueFollowUps > 0 ? '#ef4444' : '#10b981' },
  ];

  const quickActions = [
    { label: 'Add Customer', path: '/customers', icon: Users },
    { label: 'Use Calculator', path: '/calculators', icon: DollarSign },
    { label: 'View Business Plan', path: '/business-plan', icon: FileText },
    { label: 'Strategy Tools', path: '/strategy', icon: Target },
    { label: 'Marketing', path: '/marketing', icon: BarChart3 },
    { label: 'Finances', path: '/finances', icon: TrendingUp },
  ];

  const recentActivities: ActivityItem[] = [
    { id: '1', type: 'customer', message: 'New lead added', time: '2 hours ago' },
    { id: '2', type: 'goal', message: 'Weekly outreach goal achieved', time: '1 day ago' },
    { id: '3', type: 'task', message: 'Business plan updated', time: '2 days ago' },
  ];

  const tips = [
    'Set specific, measurable goals for your monthly outreach.',
    'Follow up with leads within 48 hours for best conversion rates.',
    'Use the Break-Even calculator before setting product prices.',
    'Update your SWOT analysis quarterly to stay competitive.',
    'Track customer interactions to personalize your follow-ups.',
  ];

  const randomTip = tips[Math.floor(Math.random() * tips.length)];

  // Calculate profile completion
  const hasBusinessPlan = localStorage.getItem(`expedium_answers_${user?.id}`);
  const hasCustomers = customers.length > 0;
  const hasGoals = goals.length > 0;
  const profileItems = [hasBusinessPlan, hasCustomers, hasGoals].filter(Boolean).length;
  const profileCompletion = Math.round((profileItems / 3) * 100);

  return (
    <div className="dashboard-page">
      {/* Onboarding Modal */}
      {showOnboarding && (
        <div className="onboarding-overlay">
          <div className="onboarding-modal">
            <div className="onboarding-content">
              {React.createElement(onboardingSteps[onboardingStep].icon, { size: 48, className: 'onboarding-icon' })}
              <h2>{onboardingSteps[onboardingStep].title}</h2>
              <p>{onboardingSteps[onboardingStep].description}</p>
            </div>
            <div className="onboarding-progress">
              {onboardingSteps.map((_, idx) => (
                <div key={idx} className={`progress-dot ${idx <= onboardingStep ? 'active' : ''}`} />
              ))}
            </div>
            <div className="onboarding-actions">
              {onboardingStep > 0 && (
                <button className="btn-secondary" onClick={() => setOnboardingStep(s => s - 1)}>
                  Back
                </button>
              )}
              {onboardingStep < onboardingSteps.length - 1 ? (
                <button className="btn-primary" onClick={() => setOnboardingStep(s => s + 1)}>
                  Next
                </button>
              ) : (
                <button className="btn-primary" onClick={completeOnboarding}>
                  Get Started
                </button>
              )}
            </div>
            <button className="onboarding-skip" onClick={completeOnboarding}>
              Skip Tour
            </button>
          </div>
        </div>
      )}

      {/* Header */}
      <div className="dashboard-header">
        <div className="header-content">
          <h1><LayoutDashboard size={32} /> Dashboard</h1>
          <p>Welcome back, {user?.name}! Here's your business overview.</p>
        </div>
        <div className="header-date">
          <Calendar size={18} />
          {new Date().toLocaleDateString('en-US', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' })}
        </div>
      </div>

      {/* Profile Completion */}
      {profileCompletion < 100 && (
        <div className="profile-completion-card">
          <div className="completion-info">
            <h3>Complete Your Profile</h3>
            <p>Finish setting up to unlock all features</p>
          </div>
          <div className="completion-bar-container">
            <div className="completion-bar" style={{ width: `${profileCompletion}%` }} />
          </div>
          <span className="completion-percent">{profileCompletion}%</span>
          <div className="completion-items">
            {!hasBusinessPlan && <Link to="/business-plan" className="completion-item">Complete Business Plan</Link>}
            {!hasCustomers && <Link to="/customers" className="completion-item">Add First Customer</Link>}
            {!hasGoals && <Link to="/customers" className="completion-item">Set Monthly Goals</Link>}
          </div>
        </div>
      )}

      {/* Quick Stats */}
      <div className="stats-grid">
        {quickStats.map((stat, idx) => (
          <div key={idx} className="stat-card">
            <div className="stat-icon" style={{ backgroundColor: `${stat.color}20`, color: stat.color }}>
              <stat.icon size={24} />
            </div>
            <div className="stat-info">
              <span className="stat-value">{stat.value}</span>
              <span className="stat-label">{stat.label}</span>
            </div>
            {stat.change !== undefined && (
              <div className={`stat-change ${stat.change >= 0 ? 'positive' : 'negative'}`}>
                {stat.change >= 0 ? <ArrowUpRight size={16} /> : <ArrowDownRight size={16} />}
                {Math.abs(stat.change)}%
              </div>
            )}
          </div>
        ))}
      </div>

      {/* Main Content Grid */}
      <div className="dashboard-grid">
        {/* Quick Actions */}
        <div className="dashboard-card quick-actions-card">
          <h3><Zap size={20} /> Quick Actions</h3>
          <div className="quick-actions-grid">
            {quickActions.map((action, idx) => (
              <Link key={idx} to={action.path} className="quick-action-btn">
                <action.icon size={20} />
                <span>{action.label}</span>
                <ArrowRight size={16} />
              </Link>
            ))}
          </div>
        </div>

        {/* Upcoming Follow-ups */}
        <div className="dashboard-card followups-card">
          <h3><Clock size={20} /> Upcoming Follow-ups</h3>
          {upcomingFollowUps.length > 0 ? (
            <div className="followups-list">
              {upcomingFollowUps.map((customer) => (
                <div key={customer.id} className="followup-item">
                  <div className="followup-info">
                    <span className="followup-name">{customer.name}</span>
                    <span className="followup-company">{customer.company || customer.email}</span>
                  </div>
                  <span className="followup-date">
                    {new Date(customer.nextFollowUp).toLocaleDateString()}
                  </span>
                </div>
              ))}
            </div>
          ) : (
            <div className="empty-state">
              <Clock size={32} />
              <p>No upcoming follow-ups</p>
              <Link to="/customers" className="link-btn">Add Customer</Link>
            </div>
          )}
          {overdueFollowUps > 0 && (
            <div className="overdue-alert">
              <AlertCircle size={16} />
              {overdueFollowUps} overdue follow-up{overdueFollowUps > 1 ? 's' : ''}
            </div>
          )}
        </div>

        {/* Goal Progress */}
        <div className="dashboard-card goal-card">
          <h3><Target size={20} /> Monthly Goal Progress</h3>
          {currentGoal ? (
            <div className="goal-content">
              <div className="goal-chart">
                <svg viewBox="0 0 100 100" className="circular-progress">
                  <circle cx="50" cy="50" r="45" className="progress-bg" />
                  <circle
                    cx="50"
                    cy="50"
                    r="45"
                    className="progress-fill"
                    strokeDasharray={`${goalProgress * 2.83} 283`}
                  />
                </svg>
                <div className="goal-percent">{goalProgress}%</div>
              </div>
              <div className="goal-stats">
                <div className="goal-stat">
                  <span className="stat-num">{currentGoal.completed}</span>
                  <span className="stat-text">Completed</span>
                </div>
                <div className="goal-stat">
                  <span className="stat-num">{currentGoal.targetOutreach}</span>
                  <span className="stat-text">Target</span>
                </div>
                <div className="goal-stat">
                  <span className="stat-num">{currentGoal.responses}</span>
                  <span className="stat-text">Responses</span>
                </div>
              </div>
            </div>
          ) : (
            <div className="empty-state">
              <Target size={32} />
              <p>No goal set for this month</p>
              <Link to="/customers" className="link-btn">Set Goal</Link>
            </div>
          )}
        </div>

        {/* Recent Activity */}
        <div className="dashboard-card activity-card">
          <h3><BarChart3 size={20} /> Recent Activity</h3>
          <div className="activity-list">
            {recentActivities.map((activity) => (
              <div key={activity.id} className="activity-item">
                <div className={`activity-dot ${activity.type}`} />
                <span className="activity-message">{activity.message}</span>
                <span className="activity-time">{activity.time}</span>
              </div>
            ))}
          </div>
        </div>

        {/* Business Tip */}
        <div className="dashboard-card tip-card">
          <div className="tip-icon">
            <Lightbulb size={24} />
          </div>
          <div className="tip-content">
            <h4>Tip of the Day</h4>
            <p>{randomTip}</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
